body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* fonts */
/* colors */
/* common styles */
body {
  color: #122d4b;
  font-family: 'Helvetica', Arial, sans-serif;
  font-size: 16px;
  letter-spacing: .3px; }

header,
footer {
  background-color: #122d4b;
  color: #fff; }

header {
  position: relative;
  z-index: 1;
  height: 70px;
  box-shadow: 0 2px 22px rgba(69, 69, 69, 0.4); }

.main {
  background-color: #f6f6f5;
  background-size: cover; }

.heading {
  margin-bottom: 24px; }

.row {
  display: -webkit-flex;
  display: flex; }

.side button {
  width: 100%;
  margin-bottom: 8px;
  text-align: left; }

/* typography */
button {
  border: none; }

.btn {
  font-size: 14px;
  white-space: nowrap; }
  .btn-secondary {
    background-color: white;
    box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
    font-family: "HelveticaBold", Arial, sans-serif;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2); }
  .btn-primary {
    background-color: #e4bd7d;
    color: #fff;
    box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
    font-family: "HelveticaBold", Arial, sans-serif;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2); }
  .btn-success {
    background-color: #dc5162;
    color: #fff;
    box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
    font-family: "HelveticaBold", Arial, sans-serif;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2); }
  .btn-icon {
    background-color: transparent;
    box-shadow: none; }
  .btn-sm {
    height: 32px;
    padding: 8px 12px;
    border-radius: 2px; }
  .btn-md {
    height: 36px;
    padding: 10px 15px;
    border-radius: 4px; }

input {
  border-radius: 2px;
  border: none;
  padding: 8px 15px; }

table {
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
  border-collapse: collapse;
  border-radius: 4px; }

td {
  padding: 15px 30px; }
  td:first-child {
    font-weight: bold;
    color: #dc5162; }
  td:not(:first-child) {
    width: 40%; }

tr:nth-child(2n-1) td {
  background-color: #efefef; }

th {
  padding: 15px 30px;
  text-align: left;
  font-family: "HelveticaBold", Arial, sans-serif; }

/* blocks */
.word {
  margin: 0 auto;
  padding: 50px 0;
  background-color: rgba(255, 255, 255, 0.88); }
  .word-title {
    font-family: "HelveticaBold", Arial, sans-serif;
    font-size: 18px; }
  .word-explanation {
    margin-top: 10px;
    font-size: 14px; }

/* fonts */
/* colors */
/* header */
.row-header {
  height: 70px;
  font-size: 14px;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap; }

.col-menu {
  text-align: center; }

.fa-equals {
  font-size: 32px; }

.col-logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }

.logo {
  width: 34px;
  height: 34px; }

.logo-text {
  font-family: 'Playfair Display', 'Georgia', serif;
  font-size: 25px;
  white-space: nowrap;
  margin-left: 15px; }

.search,
.translate {
  position: relative;
  font-family: 'Roboto', sans-serif;
  height: 32px; }
  .search input,
  .translate input {
    width: 100%;
    height: 100%;
    padding: 5px 12px;
    color: #122d4b; }
  .search button,
  .translate button {
    position: absolute;
    right: 5px;
    height: 100%; }
  .search .fa-search,
  .translate .fa-search {
    color: #e4bd7d;
    font-size: 20px; }
  .search .search-icon,
  .translate .search-icon {
    margin-top: 3px; }

.translate input {
  font-weight: bold;
  padding-left: 40px;
  padding-right: 30px; }

.translate i.fa-exchange-alt {
  position: absolute;
  left: 10px;
  top: 8px;
  color: #1f476b; }

.translate button {
  top: -1px;
  right: 5px;
  color: white;
  text-shadow: 0 2px #122d4b; }

.language {
  text-align: center; }
  .language .fa-chevron-down {
    position: relative;
    top: -2px;
    margin-left: 7px;
    color: #122d4b;
    text-shadow: 0 1px white; }

@media (max-width: 991px) {
  [class^=col] {
    padding-left: 10px;
    padding-right: 10px; }
  .logo-text {
    font-size: 16px; }
  .translate input {
    padding-left: 10px;
    padding-right: 20px; }
  i.fa-exchange-alt {
    display: none; } }

@media (max-width: 768px) {
  .logo-text {
    display: none; } }

@media (max-width: 480px) {
  .language {
    display: none; } }

/* fonts */
/* colors */
.footer {
  padding: 22px 0 0;
  letter-spacing: .7px; }
  .footer [class^="col-"] {
    padding-bottom: 40px; }
  .footer-brand {
    font-family: 'Playfair Display', 'Georgia', serif;
    font-size: 27px;
    white-space: nowrap;
    margin-bottom: 15px;
    letter-spacing: 1px; }
  .footer-legacy {
    font-size: 12px;
    opacity: .8;
    line-height: 1.4; }
  .footer-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin: 0 0 30px;
    padding: 0;
    font-family: "HelveticaBold", Arial, sans-serif;
    list-style-type: none; }
    .footer-menu li {
      margin-left: 20px; }
  .footer-socials {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    margin: 0;
    padding: 0;
    list-style-type: none; }
    .footer-socials li {
      margin-left: 20px; }

.translates {
  display: table;
  margin-left: auto;
  padding: 11px 25px;
  background-color: rgba(255, 255, 255, 0.17);
  border-radius: 4px;
  text-align: center; }
  .translates .title {
    font-family: "HelveticaBold", Arial, sans-serif;
    margin-bottom: 5px; }
  .translates .value {
    font-family: "HelveticaBold", Arial, sans-serif;
    font-size: 31px;
    color: #dc5162; }

@media (max-width: 768px) {
  .footer-brand,
  .footer-legacy {
    text-align: center; }
  .translates {
    margin: 0 auto; } }

@media (max-width: 991px) {
  .footer-menu,
  .footer-socials {
    -webkit-justify-content: center;
            justify-content: center; } }

/* fonts */
/* colors */
.widgets {
  padding: 50px 90px;
  background-color: rgba(255, 255, 255, 0.7); }

.widget {
  height: 200px;
  padding: 50px 90px;
  background-color: rgba(255, 255, 255, 0.7); }
  .widget-title {
    font-size: 18px;
    font-family: "HelveticaBold", Arial, sans-serif;
    margin-bottom: 10px; }
  .widget-box {
    height: 223px;
    width: 100%;
    padding: 8px;
    box-shadow: 0 0 7px rgba(18, 45, 75, 0.42);
    text-align: center;
    border-radius: 1px; }
    .widget-box.half {
      height: 98px;
      font-size: 14px; }

.col-word .widget-box {
  background-size: cover; }

.col-word .label {
  position: relative;
  margin-top: 6px;
  margin-left: -8px;
  width: 160px;
  padding: 5px 13px;
  background-color: rgba(18, 45, 75, 0.88);
  font-size: 14px;
  text-align: left;
  color: white; }
  .col-word .label .lang {
    position: absolute;
    top: 3px;
    right: 5px; }
  .col-word .label .word-title {
    margin-bottom: 5px;
    font-family: 'Playfair Display', 'Georgia', serif;
    font-size: 28px; }

.col-play .widget-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  background-size: cover; }

.col-play button {
  margin-bottom: 5px;
  padding: 8px 25px; }

.col-phrase .widget-box {
  background-color: white;
  letter-spacing: .7px; }

.col-phrase .fa-exchange-alt {
  display: block;
  height: 30px;
  padding: 7px;
  text-align: center; }

.col-phrase .flag {
  margin: 0 auto 10px; }

.col-grammar .widget-box {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  letter-spacing: .5px; }

.col-grammar img {
  position: absolute;
  top: 8px;
  right: 8px; }

.col-grammar .widget-box {
  background-color: #122d4b;
  color: white; }

